const block0 = (context) => { return html`<div class="ui5-uc-root"><div class="ui5-uc-header"><slot name="header"></slot></div><div class="ui5-uc-content"><ui5-list mode="${ifDefined(context.mode)}" @ui5-selectionChange="${ifDefined(context._onSelectionChange)}" @ui5-itemDelete="${ifDefined(context._onItemDelete)}"><slot></slot></ui5-list>${ context._showNoData ? block1(context) : block2(context) }</div></div>`; };
